nav {
    width: 100%;
    height: 64px;
    overflow: hidden;
    background-color: var(--main-color);
}

ul{
    width: 100%;
    list-style-type: none;
    display: flex;
    justify-content: center;
    color: var(--text-color);
    align-items: center;
}

a{
    margin: auto auto;
    font-size: 24px;
    color: white;
    text-decoration: none;
}