.meme {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.joke-box {
    width: 380px;
    height: 380px;
   background-color: var(--text-color);
    margin: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
}

.joke-box .joke{
    color: var(--bg-color);
    padding: 15px;
    font-size: 20px;
    
}