:root {
    --main-color : #fc543a;
    --bg-color: #1f0e0b;
    --text-color : #EDF5E1;
    --back-color: #d9513d;
}

body {
    margin: 0px;
    padding: 0px;
    background-color: var(--bg-color);
}

.btn {
    width: 150px;
    height: 40px;
    color: var(--text-color);
    border-radius: 20px;
    font-size: 16px;
    border: 1px solid var(--text-color);
    background-color: var(--main-color);
    
}

